
/* default */
.grid-wrapper {
  overflow: hidden;
}

/* xs */
@media screen and (min-width: 30rem) {
  .grid-wrapper {
    margin-left: auto;
    margin-right: auto;
    max-width: 30rem;
  }
} 

/* sm */
@media screen and (min-width: 48rem) {
  .grid-wrapper {
    max-width: 48rem;
  }
} 

/* md */
@media screen and (min-width: 62rem) {
  .grid-wrapper {
    max-width: 62rem;
  }
} 

/* lg */
@media screen and (min-width: 75rem) {
  .grid-wrapper {
    max-width: 75rem;
  }
} 
